<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/standard" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">操作标准</span>
        </el-menu-item>
        <el-menu-item index="/recordTask"   >
          <i class="el-icon-caret-right"></i>
          <span slot="title">任务记录</span>
        </el-menu-item>
        <el-menu-item index="/maintain" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">维护记录</span>
        </el-menu-item>

        <el-menu-item index="/problem" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">问题反馈</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 120px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>

    <div style="padding: 20px 0 0 20px;width: 100%">
      <div>
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </div>

      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="序号" prop="id" width="80" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备名称" prop="equipmentName" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="所属部门" prop="department"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="故障等级" prop="failureLevel" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="具体概述" width="80" prop="description"  align="center" :show-overflow-tooltip="true">
          <template v-slot="scope">
            <el-button plain @click="redirectProblem(scope.row.id,scope.row.equipmentName)" type="danger" plain size="mini">详情</el-button>
          </template>
        </el-table-column>
        <el-table-column label="反馈时间" prop="time" align="center" :show-overflow-tooltip="true"></el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="设备信息" :visible.sync="fromVisible" width="50%"  :close-on-click-modal="false">
        <el-form :model="form" label-width="80px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="设备名称" prop="equipmentName"><el-input v-model="form.equipmentName" placeholder="设备名称"></el-input></el-form-item>
          <el-form-item label="设备编号" prop="equipmentNum"><el-input v-model="form.equipmentNum" placeholder="设备编号"></el-input></el-form-item>
          <el-form-item label="任务编号" prop="taskNum"><el-input v-model="form.taskNum" placeholder="任务编号"></el-input></el-form-item>
          <div style="display: flex">
            <el-form-item label="所属部门" prop="department"><el-input v-model="form.department" placeholder="所属部门"></el-input></el-form-item>
            <el-form-item label="故障等级" prop="failureLevel"><el-input v-model="form.failureLevel" placeholder="故障等级"></el-input></el-form-item>
            <el-form-item label="任务编号" prop="taskNum"><el-input v-model="form.taskNum" placeholder="故障等级"></el-input></el-form-item>
          </div>
          <el-form-item label="具体概述" prop="description">
            <div id="editor"></div>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>


      <el-dialog title="概述" :visible.sync="fromVisible1" width="50%" >
        <div>
          <div v-html="description"></div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="fromVisible1 = false">确 定</el-button>
        </div>
        </el-dialog>
    </div>

  </div>
</template>

<script>
import E from "wangeditor"
export default {
  name: "Problem",
  data() {
    return {
      isCollapse: false,
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      equipmentName: '',
      failureLevel: '',
      taskNum:'',
      searchText: '',
      description: '',
      department: '',
      total: 0,
      fromVisible: false,
      fromVisible1: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      rules: {
        equipmentName: [
          { required: true, message: '请输入设备名称', trigger: 'blur' },
        ]
      },
ids:[],
      editor: null,
    }
  },
  created() {
     let nfcNum= this.$route.query.nfcNum;
    console.log(" Now this nfc_num is "+ nfcNum)
    if (nfcNum) {
      // 如果存在 nfc_num 参数，则根据该参数进行查询
      this.loadByNfcNum(nfcNum);
    } else {
      // 如果没有 equipment_num 参数，则显示全部数据
      this.load();
    }
  },
  methods: {
    redirectProblem(a,b) {
      // 使用 $router.push() 方法将参数作为查询参数传递给目标页面
      let encodedEquipmentName = encodeURIComponent(b);
      this.$router.push({
        path: '/problemDetail',
        query: {
          id: a,
          equipmentName: encodedEquipmentName
        }
      });

    },
    loadByNfcNum(nfcNum) {
      // 发起根据 equipment_num 查询数据的请求
      this.$request.get('/problem/selectByNfcNum', {
        params: {
          nfcNum: nfcNum,
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },
    showContent(description){
      this.description =description
      this.fromVisible1 = true
    },
    closeDialog(){
      this.editor.destroy()
      this.editor = null
    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/problem/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/problem/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    setRichText() {
      this.$nextTick(() => {
        this.editor = new E(`#editor`)
        this.editor.config.uploadImgServer = this.$baseUrl + '/file/editor/upload'
        this.editor.config.uploadFileName = 'file'
        this.editor.config.uploadImgHeaders = {
          token: this.user.token
        }
        this.editor.config.uploadImgParams = {
          type: 'img',
        }
        this.editor.config.uploadVideoServer = this.$baseUrl + '/file/editor/upload'
        this.editor.config.uploadVideoName = 'file'
        this.editor.config.uploadVideoHeaders = {
          token: this.user.token
        }
        this.editor.config.uploadVideoParams = {
          type: 'video',
        }
        this.editor.config.uploadVideoMaxSize = 100000* 1024 * 1024 * 1024 // 1024m
        this.editor.create()  // 创建
      })
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
      this.setRichText()
      this.editor.config.uploadVideoMaxSize = 100000* 1024 * 1024 * 1024 // 1024m
      setTimeout(() => {
        this.editor.txt.html(row.description)  // 设置富文本内容
      }, 0)
    },
    handleAdd() {   // 新增数据
      this.form = {  }  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
      this.setRichText()
      this.editor.config.uploadVideoMaxSize = 100000* 1024 * 1024 * 1024 // 1024m
    },
    save() {
// 获取当前 HTML 内容

      let description = this.editor.txt.html();

// 设置 form 的 description 属性
      this.form.description = description;
      // 保存按钮触发的逻辑  它会触发新增或者更新
      console.log(this.form)
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/problem/update': '/problem/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form,
          }).then(res => {
            if (res.code === '200') {  // 表示成功保存
              this.$message.success('保存成功')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 弹出错误的信息
            }
          })
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 分页查询
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/problem/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          equipmentName: this.equipmentName,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
  }
}
</script>

<style scoped>

</style>